import React, { useState } from "react";
import '../../login_admin.css';
import { useNavigate } from "react-router-dom";
import { addUser, userLogin } from "../../sevices/login.services";

export function Register () {
  const [name,setName]=useState("");
  const [email,setEmail]=useState("");
  const [password,setPassword]=useState("");
  const navigate = useNavigate();

  const validatePassword = (password) => {
    
    if (password.length < 8) {
      window.alert("     8 ");
      return false;
    }

    
    if (!/[a-z]/.test(password) || !/[A-Z]/.test(password)) {
      window.alert("          ");
      return false;
    }

    
    if (!/\d/.test(password)) {
      window.alert("      ");
      return false;
    }

    
    if (!/[^a-zA-Z0-9]/.test(password)) {
      window.alert("      ");
      return false;
    }

    return true;
  };

  async function handlForm(event){
    event.preventDefault(); 
    const user={"name":name,"email":email,"password":password, "isAdmin": true}
    try {
      if (!validatePassword(password)) {
        return;
      }
      await addUser(user);
      const response = await userLogin({ "email": email, "password": password, "isAdmin": true });
      const { userId, token, username } = response.data;
      
      localStorage.setItem("jwtToken", token);
      localStorage.setItem("userId", userId);
      localStorage.setItem("username", username);
      localStorage.setItem("email", email);
      localStorage.setItem("isAdmin", true);
      window.alert(" -  !");
      navigate("/admin/dashboard");
      window.location.reload();
    } catch (error) {
      window.alert("    .");
    }
  }
  const containerStyles = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    minHeight: '100vh',
  };

  const globalStyles = {
    boxSizing: 'border-box',
    margin: 0,
    padding: 0,
    fontFamily: 'Raleway, sans-serif',
  };
  const handleBackToHome = () => {
    navigate('/home'); 
  };

  const GoToSignIn = () => {
    navigate('/login'); 
  };

  return (
       
    <div style={globalStyles}>
      <button className="back-arrow m-2 custom-button" onClick={handleBackToHome}>
      <span></span>
    </button>
    <button className="back-arrow m-2 custom-button" onClick={GoToSignIn}>
      <span> </span>
    </button>
    <div className="container" style={containerStyles}>
      <div className="screen">
        <div className="screen__content">
          <form className="login">
            <div className="login__field">
              <i className="login__icon fas fa-user"></i>
              <input type="text" className="login__input" placeholder=" " 
              value={name} onChange={(e)=>{setName(e.target.value)}}/>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-envelope"></i>
              <input type="text" className="login__input" placeholder="" 
              value={email} onChange={(e)=>{setEmail(e.target.value)}}/>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <input type="password" className="login__input" placeholder="" 
              value={password} onChange={(e)=>{setPassword(e.target.value)}}/>
            </div>
            <button className="button login__submit" onClick={(e) => handlForm(e)}>
              <span className="button__text"> </span>
              <i className="button__icon fas fa-chevron-right"></i>
            </button>
          </form>
        </div>
        <div className="screen__background">
          <span className="screen__background__shape screen__background__shape4"></span>
          <span className="screen__background__shape screen__background__shape3"></span>
          <span className="screen__background__shape screen__background__shape2"></span>
          <span className="screen__background__shape screen__background__shape1"></span>
        </div>
      </div>
    </div>
    </div>
  );

}

