import React, { useEffect, useState } from 'react';
import { getRooms } from '../../sevices/room.services';

export function Gallery() {

    const [rooms, setRooms] = useState([]);
  const query=''

  useEffect(() => {
      fetchRooms();
    }, [query]);
  
    async function fetchRooms() {
      try {
        const res = await getRooms(query);
        setRooms(res.data);
        console.log("   :", res.data);
      } catch (error) {
        console.error("   :", error);
      }
    }

  return (
    <>
      <section className="gallery">
        <div className="container">
          <h3 className="mb-4">
            <a className="text-uppercase text-dark text-decoration-none ls-3 text-muted" href='/reservation'>
              


              
            </a>
          </h3>
          <div className="gallery-grid">

          {rooms.map((room) => (
            <div  key={room._id} className="img-holder position-relative overflow-hidden">
            <img src={`http://217.25.90.141:3005${room.image[0]}`} className="img-fluid h-100 w-100 " alt="" />

            <div className="info">
                <h5 className="text-white mt-2" >{room.name}</h5>
  
                <p className="text-white">{room.rentperday} /</p>
            </div>
            </div>
        ))}
          </div>
        </div>
      </section>
    </>
  );
}
