import React, { useState } from 'react';
import '../../main.css';
import { useNavigate } from 'react-router-dom';
import { userLogin } from '../../sevices/login.services';


export function Login() {

  const [email,setEmail]=useState("");
  const [password,setPassword]=useState("");
  const navigate = useNavigate();

  async function handlForm(event) {
    event.preventDefault();
    try {
        const response = await userLogin({ "email": email, "password": password });

        const { userId, token, username , isAdmin } = response.data;
       
        localStorage.setItem("jwtToken", token);
        localStorage.setItem("userId", userId);
        localStorage.setItem("username", username);
        localStorage.setItem("email", email);
        localStorage.setItem("isAdmin", isAdmin);
        console.log(userId);
        if(isAdmin)
       { window.alert("    !");  
        navigate("/admin/dashboard");
        window.location.reload();
       }
       else
       { window.alert("  !");  
       navigate("/reservation");
       }

    } catch (error) {
      if (error.message === " ") {
        window.alert(" . ,   .");
      } else if (error.message === "  ") {
        window.alert("  . ,     .");
      } else {
        window.alert("      !!");}
    }

  }

  return (

<section id="auth-form">
        <div className="auth--wrapper">

          <div className="bg-image">
          </div>
          <div className="form-wrapper">
            <div className="form-card">
              <h2>    </h2>
              <div className="d-none">
                  {' '}
                <a href="https://www.flaticon.com/authors/freepik" title="Freepik">
                  Freepik
                </a>{' '}
                {' '}
                <a href="https://www.flaticon.com/" title="Flaticon">
                  www.flaticon.com
                </a>
              </div>

              
              <form action="" method="post" className="form">
                <div className="form-group">
                  <label htmlFor="email"></label>
                  <input
                    type="email"
                    name="email"
                    placeholder=" "
                    className="form-control"
                    onChange={(e)=>{setEmail(e.target.value)}}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="password"></label>
                  <input
                    type="password"
                    name="password"
                    placeholder=" "
                    className="form-control"
                    onChange={(e)=>{setPassword(e.target.value)}}
                  />
                </div>
                <div className="form-group">
                  <button type="submit" onClick={(e) => handlForm(e)}  name="login-user" className="btn">
                    
                  </button>
                </div>

                <div className="alternate-auth">
                  <span>
                     ?
                    <a href="/admin/signup">&nbsp;</a>
                  </span>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
  );
};
