import React, { useState } from 'react';
import '../../main.css';
import { useNavigate } from 'react-router-dom';
import { addUser, userLogin } from '../../sevices/login.services';

export function SignUpForm() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const validatePassword = (password) => {
    
    if (password.length < 8) {
      window.alert("     8 ");
      return false;
    }

    
    if (!/[a-z]/.test(password) || !/[A-Z]/.test(password)) {
      window.alert("          ");
      return false;
    }

    
    if (!/\d/.test(password)) {
      window.alert("      ");
      return false;
    }

    
    if (!/[^a-zA-Z0-9]/.test(password)) {
      window.alert("      ");
      return false;
    }

    return true;
  };

  async function handleForm(event) {
    event.preventDefault();
    const user = { "name": name, "email": email, "password": password };
    try {
      if (!validatePassword(password)) {
        return;
      }
      
      await addUser(user);
      const response = await userLogin({ "email": email, "password": password });
      const { userId, token, username } = response.data;
      
      localStorage.setItem("jwtToken", token);
      localStorage.setItem("userId", userId);
      localStorage.setItem("username", username);
      localStorage.setItem("email", email);
      window.alert("   !");
      navigate("/home");
    } catch (error) {
      window.alert("    .");
    }
  }

  return (
    <section id="signup-form">
      <div className="auth--wrapper">
        <div className="form-wrapper">
          <div className="form-card">
            <h2></h2>
            <form action="" method="post" className="form">
              <div className="part part-3">
                <div className="form-group">
                  <label htmlFor="username"></label>
                  <input
                    type="text"
                    name="username"
                    placeholder=" "
                    className="form-control"
                    value={name} onChange={(e) => { setName(e.target.value) }}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="email"></label>
                  <input
                    type="email"
                    name="email"
                    placeholder=" "
                    className="form-control"
                    value={email} onChange={(e) => { setEmail(e.target.value) }}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="password"></label>
                  <input
                    type="password"
                    name="password"
                    placeholder=" "
                    className="form-control"
                    value={password} onChange={(e) => { setPassword(e.target.value) }} />
                </div>

                <div className="form-group">
                  <button type="submit" onClick={(e) => handleForm(e)} name="login-user" className="btn">
                    
                  </button>

                </div>
              </div>
              <div className="alternate-auth">
                <span>
                       ?
                  <a href="/login">&nbsp;</a>
                </span>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};


