import React, { useState, useEffect, useCallback } from 'react';
import { cancelBooking, getBookings } from '../../sevices/booking.services';
import Swal from 'sweetalert2';
import { Divider, Space, Tag } from 'antd';


export function Bookings() {
  const [bookings, setBookings] = useState([]);
  const userId = localStorage.getItem("userId");

  const fetchUserBookings = useCallback(async () => {
    try {
      const response = await getBookings(userId);
      setBookings(response.data);
    } catch (error) {
      console.error("    :", error);
    }
  }, [userId]);

  useEffect(() => {
    fetchUserBookings();
  }, [userId, fetchUserBookings]);


  async function handleCancelBooking(bookingId) {
    try {
      await cancelBooking(bookingId);
      fetchUserBookings();
      Swal.fire('Congrats', '    ', 'success').then(() => {
        window.location.reload()
      });
    } catch (error) {
      console.error("   :", error);
      Swal.fire('Oops', '-  ', 'error');
    }
  }

  return (
    <div className="container mt-4">
      <h4 className="mb-3"> </h4>
      <Space direction="vertical" style={{ width: '100%' }}>
        {bookings.map((booking) => (
          <div key={booking._id} className="card shadow rounded mb-4">
            <div className="card-body">
              <h5 className="card-title">{booking.room.name}</h5>
              <p><strong> :</strong> {booking._id}</p>
              <p><strong>:</strong> {booking.fromdate}</p>
              <p><strong>:</strong> {booking.todate}</p>
              <p><strong> :</strong> {booking.totalamount} </p>
              <p><strong>: </strong>
                {(booking.status === 'booked') ? <Tag color="green"></Tag> : <Tag color="red"></Tag>}</p>
            </div>
            {booking.status !== 'canceled' && (
              <div className='text-right mr-4 mb-4'>
                <button className="btn btn-danger" onClick={() => { handleCancelBooking(booking._id) }}> </button>
              </div>
            )}
          </div>
        ))}
      </Space>
      <Divider />
    </div>
  );
}
