import React, { useEffect, useState, useCallback } from 'react';
import Swal from 'sweetalert2';
import { cancelBooking, deleteBookingByID, getBooking } from '../../sevices/booking.services';
import { NavBar } from './component.nav';
import { AdminSidebar } from './component.sidebar';
import { Form } from 'react-bootstrap';

export function BookingList() {
  const [bookings, setBookings] = useState([]);
  const [query, setQuery] = useState('');

  const fetchBookings = useCallback(async () => {
    try {
      const response = await getBooking(query);
      setBookings(response.data);
    } catch (error) {
      console.error('   ', error);
    }
  }, [query]);

  useEffect(() => {
    fetchBookings();
  }, [fetchBookings]);

  async function handleCancelBooking(bookingId) {
    try {
      await cancelBooking(bookingId);
      Swal.fire('Congrats', '    ', 'success').then(result => {
        window.location.reload();
      });
    } catch (error) {
      console.error('   :', error);
      Swal.fire('Oops', '-   ', 'error');
    }
  }

  async function deleteBooking(id) {
    try {
      await deleteBookingByID(id);
      fetchBookings();
      Swal.fire('!', '    ', 'success').then(result => {
        window.location.reload();
      });
    } catch (error) {
      console.error("   :", error);
      Swal.fire('Oops', '-   ', 'error');

    }
  }

  return (
    <>
      <NavBar />
      <AdminSidebar />

      <main id="main" className="flexbox-col p-5">
        <h2 className="center mb-2 ml-5"> </h2>
        <Form.Control type="search" className="w-50 m-3" onChange={e => setQuery(e.target.value)} placeholder="  " />
        <table className="table table-striped">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th> </th>
              <th> </th>
              <th> </th>
              <th> </th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {bookings.map((booking, index) => (
              <tr key={booking._id} className={index % 2 === 0 ? 'even-row' : 'odd-row'}>
                <td>{booking.room.name}</td>
                <td><img 
    height={100} 
    width={100} 
    src={`http://217.25.90.141:3005${booking.room.image[0]}`} 
    alt={`  ${booking.room.name}`} 
  /></td>
                <td>{booking.user.name}</td>
                <td>{booking.fromdate}</td>
                <td>{booking.todate}</td>
                <td>{booking.totalamount}</td>
                <td>{booking.totaldays}</td>
                <td>
                  {booking.status}
                </td>
                <td>
                <div >
                  {booking.status === 'booked' && (
                    <button
                      className="btn btn-danger btn-sm"
                      onClick={() => handleCancelBooking(booking._id)}
                    >
                      
                    </button>
                  )}

<button className="btn btn-danger btn-sm ml-3" onClick={() => deleteBooking(booking._id)}>
                  
                </button>
                </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </main>
    </>
  );
}
